* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.block-container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  flex-grow: 1;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.block-1 {
  background-color: red;
}

.block-2 {
  background-color: rgb(80%, 0%, 0%);
}

.block-3 {
  background-color: rgb(60%, 0%, 0%);
}

.block-4 {
  background-color: rgb(40%, 0%, 0%);
}

.block-5 {
  background-color: rgb(20%, 0%, 0%);
}

.block-6 {
  background-color: rgb(0%, 0%, 0%);
}

@media (min-width: 600px) and (max-width: 899px) {
  .block {
    flex-basis: 50%;
  }

  .block-3 {
    order: 1;
  }

  .block-5 {
    order: 2;
  }

  .block-6 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.33%;
  }

  .block-6 {
    order: 1;
  }

  .block-5 {
    order: 2;
  }

  .block-4 {
    order: 3;
  }
}
